<template>
  <div class="homepage">
    <header>
      <j-swiper
        :list="list"
        height="100%"
        :interval="3000"
        :duration="366"
        :auto="true">
      </j-swiper>
    </header>
    <main>
      <nav>
        <span>热门产品</span>
        <span>最新</span>
        <span>状态</span>
      </nav>
      <div class="content">
        <div class="list wave"
          v-for="(obj, key) in dataList"
          :key='key'
          @click="jump(obj)">
          <img :src="obj.img" alt="">
          <ul>
            <li>
              <span>{{ obj.cname }}</span>
              <span>{{ obj.ename }}</span>
            </li>
            <li><span :style="{backgroundColor:obj.sorted}">{{obj.price}}</span></li>
            <li class="right-arrow"><span :style="obj.closes=='休市'?'background:#3a8ee6':''">{{ obj.closes }}</span></li>
          </ul>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
// @ is an alias to /src
import { getSalesman } from '@/utils/auth'
import JSwiper from '@/components/JSwiper'

export default {
  name: 'homepage',
  components: {
    JSwiper
  },
  data () {
    return {
      dataList: [],
      timer: '',
      list: [{
        url: 'javascript:',
        img: 'https://app.nmwdds.cn/img/t_imgs/home/1.png'
      }, {
        url: 'javascript:',
        img: 'https://app.nmwdds.cn/img/t_imgs/home/2.png'
      }, {
        url: 'javascript:',
        img: 'https://app.nmwdds.cn/img/t_imgs/home/3.png'
      }, {
        url: 'javascript:',
        img: 'https://app.nmwdds.cn/img/t_imgs/home/4.png'
      }, {
        url: 'javascript:',
        img: 'https://app.nmwdds.cn/img/t_imgs/home/5.png'
      }, {
        url: 'javascript:',
        img: 'https://app.nmwdds.cn/img/t_imgs/home/6.png',
        fallbackImg: 'https://app.nmwdds.cn/img/t_imgs/home/1.png'
      }]
    }
  },
  created () {
    // this.$loading()
    this.init(true)
    this.circulation()
  },
  methods: {
    init (loading) {
      this.request({
        url: '/index',
        method: 'get',
        loading
      }).then(res => {
        if (res.code === 1) {
          this.dataList = res.data
        }
      })
    },
    circulation () {
      this.timer = setInterval(() => {
        this.init(false)
      }, 3000)
    },
    jump (obj) {
      if (obj.closes === '休市' && getSalesman() === 10) {
        this.$toast('产品休市中')
      } else if (obj.closes === '开市' || getSalesman() === 2) {
        this.$router.push({ name: 'details', params: obj })
      }
    }
  },
  mounted () {
  },
  destroyed () {
    clearInterval(this.timer)
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
